.channel-list-group {
    width: 1200px;
    margin: 0 auto;
}
.channel-list-group h2 {
    color: #fff;
    font-weight: 400;
    margin-top: 20px;
}
.channel-list__slider ul {
    display: flex;
    margin-top: 30px;
    flex-wrap: wrap;
}
.channel-list__slider ul li a {
    display: block;
    padding: 10px 20px;
    font-size: 18px;
    background: #3d4958;
    border: 1px solid #3d4958;
    margin: 0 10px 10px 0;
    color: #fff;
    cursor: pointer;
}
.channel-list__slider ul li a {
    cursor: pointer;
}
.channel-list__slider ul li a.active {
    background-color: #282e34;
}
@media screen and (max-width: 1220px) {
    .channel-list-group {
        width: 1000px;
        margin: 0 auto;
    }
}
@media screen and (max-width: 1000px) {
    .channel-list-group {
        width: 800px;
        margin: 0 auto;
    }
}
@media screen and (max-width: 800px) {
    .channel-list-group {
        width: 600px;
        margin: 0 auto;
    }
}
@media screen and (max-width: 800px) {
    .channel-list-group {
        width: 600px;
        margin: 0 auto;
    }
}
@media screen and (max-width: 600px) {
    .channel-list-group {
        width: 500px;
        margin: 0 auto;
    }
    .channel-list__slider ul li a {
        display: block;
        padding: 10px 10px;
        font-size: 16px;
        background: #3d4958;
        border: 1px solid #3d4958;
        margin: 0 10px 10px 0;
        color: #fff;
        cursor: pointer;
    }
}
@media screen and (max-width: 495px) {
    .channel-list-group {
        width: 400px;
        margin: 0 auto;
    }
    .channel-list__slider ul li a {
        display: block;
        padding: 8px;
        font-size: 14px;
        background: #3d4958;
        border: 1px solid #3d4958;
        margin: 0 10px 10px 0;
        color: #fff;
        cursor: pointer;
    }
}